import React,{useState} from 'react' 
import PasswordAlert from '../PasswordAlert';
import EmailAlert from './alerts/EmailAlert';
import FullNameAlert from './alerts/FullNameAlert';
const Input = () => {
    const [inputFields, setInputFields] = useState({
        name: '',
        email: '',
        password: '',
        fullNameAlert: null,
        emailAlert: null,
        passwordAlert: null
    })
    const{name, email, password, fullNameAlert, emailAlert, passwordAlert} = inputFields;
    const onChange = (e) => {
        setInputFields({...inputFields, [e.target.name]: e.target.value})
    }
    const re = /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
    const onSubmit =(e) => {
        e.preventDefault();
        if(name.trim() === '' && !re.test(email) && password < 6){
            setInputFields({...inputFields, fullNameAlert: !null, emailAlert: !null, passwordAlert: !null})
        }else if(name.trim() !== ''){
            setInputFields({...inputFields, fullNameAlert: null})
        }
        if(re.test(email)){
            setInputFields({...inputFields, emailAlert: null})
        }
        if(password.length > 6){
            setInputFields({...inputFields, passwordAlert: null})
        }
        if(name.trim() !== '' && re.test(email) && password.length > 6){
            alert('Login Succesfully')
            setInputFields({...inputFields, name: '', email: '', password: ''})
        }
    }
    return (
        <form onSubmit={onSubmit}>
            <label>Full Name</label>
            <div>
            <input type="text" placeholder='Please Enter your username' name='name' value={name} onChange={onChange}/>
            <FullNameAlert fullNameAlert={fullNameAlert}></FullNameAlert>
            </div>
            <label>Email</label>
            <div>
            <input type="email" placeholder='Please Enter your email'name='email' value={email} onChange={onChange}/>
            <EmailAlert emailAlert={emailAlert}></EmailAlert>
            </div>
            <label>Password</label>
            <div>
            <input type="password" placeholder='Please Enter your password' name='password' value={password} onChange={onChange}/>
            <PasswordAlert passwordAlert={passwordAlert}></PasswordAlert>
            </div>
            <input type="submit" value="SIGN IN" />
        </form>
    )
}

export default Input
